    width: 100px;
    height: 150px;
    border-radius: 50%;
}

@media print {
    .codeblock, pre.src {
        /* white-space: pre.src-wrap */
    }
}

@media print {
    html, body, section {
        background: none !important
    }
    * {
        box-shadow: none !important;
        text-shadow: none !important;
        filter: none !important;
        -ms-filter: none !important
    }
    a, a:visited {
        text-decoration: underline
    }
    pre.src, blockquote {
        page-break-inside: avoid
    }
    thead {
        display: table-header-group
    }
    tr, img {
        page-break-inside: avoid
    }
    img {
        max-width: 100% !important
    }

    @page {
        margin: 0.5cm
    }
    p, h2, h3 {
        orphans: 3;
        widows: 3
    }
    h2, h3 {
        page-break-after: avoid
    }
}

@media print {
    #postamble {
        display: none
    }
    #content {
        margin-left: 0
    }
}

@media print {
    #table-of-contents {
        display: none
    }
    @page {
        size: auto;
        margin: 25mm 25mm 25mm 25mm;
    }
    body {
        margin: 0px;
    }
}

@media screen and (max-width: 768px) {}

@media only screen and (max-width: 480px) {}

@media screen and (max-width: 768px) {
    .tablet-hide {
        display: none
    }
}

@media screen and (max-width: 480px) {
    .mobile-hide {
        display: none
    }
}

@media screen and (max-width: 480px) {}

@media screen and (max-width: 768px) {
    #content {
        margin-left: 0
    }
    #content #content {
        padding: 1.618em
    }
    #content.shift {
        position: fixed;
        min-width: 100%;
        left: 85%;
        top: 0;
        height: 100%;
        overflow: hidden
    }
}

@media screen and (min-width: 1400px) {
    #content {
        background: rgba(0, 0, 0, 0.05)
    }
    #content {
        background: #fcfcfc
    }
}

@media screen and (max-width: 768px) {
    #copyright {
        width: 85%;
        display: none
    }
    #copyright.shift {
        display: block
    }
    img {
        width: 100%;
        height: auto
    }
}

@media screen and (max-width: 480px) {
    #content .sidebar {
        width: 100%
    }
}

